import React, { useState } from "react";

const styles = {
  chatIcon: {
    position: "fixed",
    bottom: "20px",
    right: "20px",
    width: "48px",
    height: "48px",
    borderRadius: "50%",
    backgroundColor: "#007bff",
    color: "#fff",
    fontSize: "24px",
    border: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    cursor: "pointer",
    boxShadow: "0 2px 8px rgba(0,0,0,0.2)",
    zIndex: 9999,
  },
  chatBox: {
    position: "fixed",
    bottom: "80px",
    right: "20px",
    width: "350px",
    maxHeight: "500px",
    backgroundColor: "#fff",
    border: "1px solid #ccc",
    borderRadius: "10px",
    padding: "10px",
    fontFamily: "Arial, sans-serif",
    fontSize: "13px",
    boxShadow: "0 2px 12px rgba(0,0,0,0.15)",
    display: "flex",
    flexDirection: "column",
    zIndex: 9999,
  },
  chatArea: {
    flex: 1,
    overflowY: "auto",
    marginBottom: "10px",
  },
  inputRow: {
    display: "flex",
    gap: "8px",
  },
  inputBox: {
    flex: 1,
    padding: "6px",
    borderRadius: "4px",
    border: "1px solid #ccc",
  },
  sendBtn: {
    backgroundColor: "#007bff",
    color: "white",
    border: "none",
    padding: "6px 10px",
    borderRadius: "4px",
    cursor: "pointer",
  },
  messageBot: {
    backgroundColor: "#f1f1f1",
    padding: "6px 10px",
    borderRadius: "6px",
    marginBottom: "6px",
    alignSelf: "flex-start",
  },
  messageUser: {
    backgroundColor: "#007bff",
    color: "#fff",
    padding: "6px 10px",
    borderRadius: "6px",
    marginBottom: "6px",
    alignSelf: "flex-end",
  },
};

const SmartWeeklyPlanChatbot = () => {
  const [open, setOpen] = useState(false);
  const [messages, setMessages] = useState([
    { type: "bot", text: "Hi 👋! What would you like to do?" },
    {
      type: "suggestions",
      buttons: [
        { label: "📄 View Plan", value: "view" },
        { label: "➕ Add New Order", value: "add" },
        { label: "✏️ Update Order", value: "update" },
      ],
    },
  ]);
  const [input, setInput] = useState("");
  const [step, setStep] = useState("idle");
  const [formData, setFormData] = useState({});
  const [currentField, setCurrentField] = useState("");
  const [allData, setAllData] = useState([]);

  const handleSuggestionClick = (value) => {
    setInput(value);
    setTimeout(() => handleSend(), 0);
  };

  const handleSend = async () => {
    if (!input.trim()) return;
    const userMsg = { type: "user", text: input };
    const updatedMessages = [...messages, userMsg];
    setMessages(updatedMessages);
    const userInput = input.trim();
    setInput("");

    if (step === "idle") {
      if (userInput === "view") {
        try {
          const res = await fetch("http://localhost:8080/internal/weekly_plan");
          const data = await res.json();
          setAllData(data);
          const formatted = data.slice(0, 2).map((item, i) => `${i + 1}. ${item.main_prod_no} - ${item.customer_name} (${item.week_prod_date.slice(0, 10)})`).join("\n");
          setMessages((prev) => [...prev, { type: "bot", text: `Here are some plans:\n${formatted}` }]);
        } catch {
          setMessages((prev) => [...prev, { type: "bot", text: "❌ Failed to fetch data." }]);
        }
      } else if (userInput === "add") {
        setStep("add_plant_code");
        setFormData({});
        setCurrentField("plant_code");
        setMessages((prev) => [...prev, { type: "bot", text: "Let's add a new production order.\nEnter Plant Code:" }]);
      } else if (userInput === "update") {
        setStep("update_ask_prod_no");
        setMessages((prev) => [...prev, { type: "bot", text: "Please enter the Production Order No. you want to update:" }]);
      } else {
        setMessages((prev) => [...prev, { type: "bot", text: "❓ Please type or click: `view`, `add`, or `update`." }]);
      }
    }

    else if (step.startsWith("add_")) {
      const fieldMap = [
        "plant_code", "main_prod_no", "main_forge_press", "week_prod_date", "heat_code",
        "shift1_qty", "shift2_qty", "shift3_qty", "dies_req", "rm_status",
        "week_net_tonn", "remark", "week_die_no", "customer_name", "week_section", "rm_grade"
      ];
      const currentIdx = fieldMap.indexOf(currentField);
      const nextField = fieldMap[currentIdx + 1];
      const updatedForm = { ...formData, [currentField]: userInput };
      setFormData(updatedForm);

      if (!nextField) {
        try {
          const res = await fetch("http://localhost:8080/internal/weekly_plan", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(updatedForm),
          });
          if (res.ok) {
            setMessages((prev) => [...prev, { type: "bot", text: "✅ Order successfully added!" }]);
          } else {
            setMessages((prev) => [...prev, { type: "bot", text: "❌ Failed to add order." }]);
          }
        } catch {
          setMessages((prev) => [...prev, { type: "bot", text: "❌ Network error." }]);
        }
        setStep("idle");
        setFormData({});
      } else {
        setCurrentField(nextField);
        setStep("add_" + nextField);
        const label = nextField.replace(/_/g, " ");
        setMessages((prev) => [...prev, { type: "bot", text: `Enter ${label}:` }]);
      }
    }

    else if (step === "update_ask_prod_no") {
      try {
        const res = await fetch("http://localhost:8080/internal/weekly_plan");
        const data = await res.json();
        setAllData(data);
        const order = data.find((item) => item.main_prod_no === userInput);
        if (!order) {
          setMessages((prev) => [...prev, { type: "bot", text: "❌ Order not found. Try again." }]);
        } else {
          setFormData(order);
          setStep("update_field_select");
          const summary = Object.entries(order).slice(0, 6).map(([k, v]) => `${k}: ${v}`).join("\n");
          setMessages((prev) => [...prev, { type: "bot", text: `Order found:\n${summary}\nWhich field would you like to update?` }]);
        }
      } catch {
        setMessages((prev) => [...prev, { type: "bot", text: "❌ Error fetching data." }]);
      }
    } else if (step === "update_field_select") {
      if (!formData.hasOwnProperty(userInput)) {
        setMessages((prev) => [...prev, { type: "bot", text: "❌ Invalid field. Try again." }]);
      } else {
        setCurrentField(userInput);
        setStep("update_field_value");
        setMessages((prev) => [...prev, { type: "bot", text: `Enter new value for ${userInput}:` }]);
      }
    } else if (step === "update_field_value") {
      const patchData = {
        prod_order: formData.main_prod_no,
        [currentField]: userInput,
      };
      try {
        const res = await fetch("http://localhost:8080/internal/weekly_plan", {
          method: "PATCH",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(patchData),
        });
        if (res.ok) {
          setMessages((prev) => [...prev, { type: "bot", text: `✅ ${currentField} updated.` }]);
        } else {
          setMessages((prev) => [...prev, { type: "bot", text: "❌ Failed to update." }]);
        }
      } catch {
        setMessages((prev) => [...prev, { type: "bot", text: "❌ Network error." }]);
      }
      setStep("idle");
      setFormData({});
    }
  };

  return (
    <>
      <button style={styles.chatIcon} onClick={() => setOpen(!open)}>
        💬
      </button>

      {open && (
        <div style={styles.chatBox}>
          <div style={styles.chatArea}>
            {messages.map((msg, idx) => {
              if (msg.type === "bot") {
                return <div key={idx} style={styles.messageBot}>{msg.text}</div>;
              } else if (msg.type === "user") {
                return <div key={idx} style={styles.messageUser}>{msg.text}</div>;
              } else if (msg.type === "suggestions") {
                return (
                  <div key={idx} style={styles.messageBot}>
                    {msg.buttons.map((btn, i) => (
                      <button
                        key={i}
                        style={{
                          display: "inline-block",
                          margin: "4px 6px 0 0",
                          padding: "4px 8px",
                          fontSize: "12px",
                          backgroundColor: "#e2e6ea",
                          border: "1px solid #ccc",
                          borderRadius: "4px",
                          cursor: "pointer",
                        }}
                        onClick={() => handleSuggestionClick(btn.value)}
                      >
                        {btn.label}
                      </button>
                    ))}
                  </div>
                );
              }
              return null;
            })}
          </div>
          <div style={styles.inputRow}>
            <input
              style={styles.inputBox}
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="Type here..."
              onKeyDown={(e) => e.key === "Enter" && handleSend()}
            />
            <button style={styles.sendBtn} onClick={handleSend}>Send</button>
          </div>
        </div>
      )}
    </>
  );
};

export default SmartWeeklyPlanChatbot;
