import React, { useState } from "react";
import { BarChart3, Calendar, Target } from "lucide-react";
import "./Dashboard.css";
import MainDashboard from "./MainDashboard";
import WeeklyPlan from "./WeeklyPlan.jsx";
import DieSummary from "./DieSummary";

const Dashboard = () => {
  const [activeView, setActiveView] = useState("main");

  return (
    <div className="dashboard-container">
      <div className="dashboard-content">
        <div className="dashboard-header">
          <div>
            <h1 className="dashboard-title">Forging Plan Dashboard</h1>
            <p className="dashboard-subtitle">Manage your production planning efficiently</p>
          </div>
          <div className="view-toggle">
            <button onClick={() => setActiveView("main")} className={`toggle-button ${activeView === "main" ? "active" : ""}`}>
              <BarChart3 className="icon-sm inline" /> Main Dashboard
            </button>
            <button onClick={() => setActiveView("weekly")} className={`toggle-button ${activeView === "weekly" ? "active" : ""}`}>
              <Calendar className="icon-sm inline" /> Weekly Plan
            </button>
            <button onClick={() => setActiveView("summary")} className={`toggle-button ${activeView === "summary" ? "active" : ""}`}>
              <Target className="icon-sm inline" /> Die Summary
            </button>
          </div>
        </div>

        {activeView === "main" && <MainDashboard />}
        {activeView === "weekly" && <WeeklyPlan />}
        {activeView === "summary" && <DieSummary />}
      </div>
    </div>
  );
};

export default Dashboard;
