import React, { useState, useEffect } from "react";
import { Edit, Save, X } from "lucide-react";
import "./WeeklyPlanDisplay.css";

// Function to safely convert string to integer
const safeParseInt = (value, defaultValue = 0) => {
  if (value === null || value === undefined || value === "") {
    return defaultValue;
  }
  const parsed = parseInt(value, 10);
  return isNaN(parsed) ? defaultValue : parsed;
};

// Function to safely convert string to float
const safeParseFloat = (value, defaultValue = 0) => {
  if (value === null || value === undefined || value === "") {
    return defaultValue;
  }
  const parsed = parseFloat(value);
  return isNaN(parsed) ? defaultValue : parsed;
};

// Function to safely convert to string
const safeParseString = (value, defaultValue = "") => {
  if (value === null || value === undefined) {
    return defaultValue;
  }
  return String(value).trim();
};

// Function to validate form data
const validateFormData = (formData) => {
  const errors = [];

  // Required string fields
  if (!safeParseString(formData.prod_order)) {
    errors.push("Production Order is required");
  }

  // Numeric validations
  const plantCode = safeParseInt(formData.plant_code);
  if (plantCode <= 0) {
    errors.push("Plant Code must be a positive number");
  }

  const prodTonn = safeParseFloat(formData.prod_tonn);
  if (prodTonn < 0) {
    errors.push("Production Tonnage cannot be negative");
  }

  // Quantity validations
  const shift1 = safeParseInt(formData.shift1_qty);
  const shift2 = safeParseInt(formData.shift2_qty);
  const shift3 = safeParseInt(formData.shift3_qty);

  if (shift1 < 0 || shift2 < 0 || shift3 < 0) {
    errors.push("Shift quantities cannot be negative");
  }

  return errors;
};

// Function to map API data to component format
const mapApiDataToComponentFormat = (apiData) => {
  return apiData.map((item) => ({
    plantCode:
      item.plant_code !== null &&
      item.plant_code !== undefined &&
      item.plant_code !== 0
        ? item.plant_code
        : "",
    productionOrderNo: item.main_prod_no || "",
    pressId: item.main_forge_press || "",
    customer:
      item.customer_name && item.customer_name !== "None"
        ? item.customer_name
        : "",
    netWt:
      item.week_net_tonn !== null &&
      item.week_net_tonn !== undefined &&
      item.week_net_tonn !== 0
        ? item.week_net_tonn
        : "",
    dieNo: item.week_die_no || "",
    qty: formatQuantity(item.shift1_qty, item.shift2_qty, item.shift3_qty),
    shift1Qty: item.shift1_qty || 0,
    shift2Qty: item.shift2_qty || 0,
    shift3Qty: item.shift3_qty || 0,
    prodTonn:
      item.week_net_tonn !== null &&
      item.week_net_tonn !== undefined &&
      item.week_net_tonn !== 0
        ? item.week_net_tonn
        : "",
    section:
      item.week_section !== null &&
      item.week_section !== undefined &&
      item.week_section !== 0
        ? item.week_section
        : "",
    grade: item.rm_grade && item.rm_grade !== "None" ? item.rm_grade : "",
    dieRequired:
      item.dies_req !== null &&
      item.dies_req !== undefined &&
      item.dies_req !== 0
        ? item.dies_req
        : "",
    rmStatus: item.rm_status && item.rm_status !== "None" ? item.rm_status : "",
    heatCode: item.heat_code && item.heat_code !== "None" ? item.heat_code : "",
    remark: item.remark && item.remark !== "None" ? item.remark : "",
    date: item.week_prod_date,
    rawData: item,
    id: item.id || `${item.main_prod_no}-${item.week_prod_date}`,
  }));
};

// Function to format quantity display
const formatQuantity = (shift1, shift2, shift3) => {
  const s1 = shift1 !== null && shift1 !== undefined ? shift1 : 0;
  const s2 = shift2 !== null && shift2 !== undefined ? shift2 : 0;
  const s3 = shift3 !== null && shift3 !== undefined ? shift3 : 0;

  // If all shifts are zero, return empty
  if (s1 === 0 && s2 === 0 && s3 === 0) return "";

  return [s1, s2, s3].join(", ");
};

// Function to group data by day of week
const groupDataByDay = (mappedData) => {
  const dayNames = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
  const workingDays = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
  const grouped = {};

  // Initialize all working days
  workingDays.forEach((day) => {
    grouped[day] = [];
  });

  mappedData.forEach((item) => {
    const date = new Date(item.date);
    const dayName = dayNames[date.getDay()];

    if (workingDays.includes(dayName)) {
      grouped[dayName].push({
        ...item,
        dayLabel: `${dayName} (${new Date(item.date).toLocaleDateString(
          "en-GB",
          {
            day: "2-digit",
            month: "short",
          }
        )})`,
      });
    }
  });

  return grouped;
};

// Function to get date range for week title
const getWeekDateRange = (apiData) => {
  if (!apiData.length) return "Loading...";

  const dates = apiData.map((item) => new Date(item.week_prod_date));
  const minDate = new Date(Math.min(...dates));
  const maxDate = new Date(Math.max(...dates));

  const formatDate = (date) => {
    return date.toLocaleDateString("en-GB", {
      day: "2-digit",
      month: "short",
      year: "numeric",
    });
  };

  return `${formatDate(minDate)} - ${formatDate(maxDate)}`;
};

const WeeklyPlanDisplay = () => {
  const [apiData, setApiData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [groupedPlans, setGroupedPlans] = useState({});
  const [weekTitle, setWeekTitle] = useState("Loading...");
  const [editingRow, setEditingRow] = useState(null);
  const [editFormData, setEditFormData] = useState({});
  const [message, setMessage] = useState({ type: "", text: "" });
  const [saving, setSaving] = useState(false);
  const [validationErrors, setValidationErrors] = useState([]);
  const [dieOptions, setDieOptions] = useState([]);
  const [showDieOptions, setShowDieOptions] = useState(false);
  const [matchingDieOptions, setMatchingDieOptions] = useState([]);

  // Fetch data from API
  const fetchWeeklyPlan = async () => {
    try {
      setLoading(true);
      setError(null);

      const response = await fetch(
        "http://localhost:8080/internal/weekly_plan"
      );

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      setApiData(data);

      // Map and group the data
      const mappedData = mapApiDataToComponentFormat(data);
      const grouped = groupDataByDay(mappedData);
      setGroupedPlans(grouped);

      // Set week title
      const dateRange = getWeekDateRange(data);
      setWeekTitle(`Weekly Plan Preview - ${dateRange}`);
    } catch (err) {
      console.error("Error fetching weekly plan:", err);
      setError(`Failed to load weekly plan: ${err.message}`);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchWeeklyPlan();
  }, []);

  // Handle edit button click
  const handleEditClick = (dayName, planIndex, plan) => {
    const rowKey = `${dayName}-${planIndex}`;
    setEditingRow(rowKey);
    setValidationErrors([]);

    setEditFormData({
      prod_order: plan.productionOrderNo || "P-000005",
      die_no: plan.dieNo || "DIE456",
      plant_code: plan.plantCode || 2101,
      forge_press: plan.pressId || "FP3000",
      customer: plan.customer || "XYZ",
      shift1_qty: plan.shift1Qty || 100,
      shift2_qty: plan.shift2Qty || 120,
      shift3_qty: plan.shift3Qty || 80,
      prod_tonn: plan.prodTonn || 15.5,
      section: plan.section || 90,
      rm_grade: plan.grade || "GR-B",
      die_req: plan.dieRequired || 2,
      rm_status: plan.rmStatus || "Available",
      heat_code: plan.heatCode || "HC001",
      remark: plan.remark || "Ready",
    });
  };

  const handleFieldChange = async (field, value) => {
    setEditFormData((prev) => ({
      ...prev,
      [field]: value,
    }));

    if (validationErrors.length > 0) {
      setValidationErrors([]);
    }
    if (field === "die_no" && value.trim() !== "") {
      try {
        const res = await fetch(
          `http://localhost:8080/internal/weekly_entry?die_no=${encodeURIComponent(
            value
          )}`
        );
        const data = await res.json();
        const results = data || [];

        console.log("Fetched die records from weekly_entry:", results);

        if (results.length === 1) {
          const item = results[0];
          setEditFormData((prev) => ({
            ...prev,
            die_no: item.die_no || "",
            plant_code: item.plant_code || "",
            forge_press: item.forge_press || "",
            customer: item.customer || "",
            prod_tonn: item.net_wt || "",
            section: item.section || "",
            rm_grade: item.rm_grade || "",
            die_req: item.die_req || "",
            rm_status: item.rm_status || "",
          }));
          setMatchingDieOptions([]);
        } else if (results.length > 1) {
          setMatchingDieOptions(results); // show dropdown
        } else {
          setMatchingDieOptions([]);
        }
      } catch (err) {
        console.error("Error fetching from weekly_entry:", err);
        setMatchingDieOptions([]);
      }
    }
  };

  // Handle save
  const handleSave = async (dayName, planIndex, originalPlan) => {
    try {
      setSaving(true);
      setMessage({ type: "", text: "" });
      setValidationErrors([]);

      // Validate form data
      const errors = validateFormData(editFormData);
      if (errors.length > 0) {
        setValidationErrors(errors);
        setSaving(false);
        return;
      }

      // Prepare data for API with proper data types
      const updateData = {
        id: originalPlan.id, // ✅ Add this line
        prod_order: safeParseString(editFormData.prod_order),
        die_no: safeParseString(editFormData.die_no),
        plant_code: safeParseInt(editFormData.plant_code, 0),
        forge_press: safeParseString(editFormData.forge_press),
        customer: safeParseString(editFormData.customer),
        shift1_qty: safeParseInt(editFormData.shift1_qty, 0),
        shift2_qty: safeParseInt(editFormData.shift2_qty, 0),
        shift3_qty: safeParseInt(editFormData.shift3_qty, 0),
        prod_tonn: safeParseFloat(editFormData.prod_tonn, 0),
        section: safeParseInt(editFormData.section, 0),
        rm_grade: safeParseString(editFormData.rm_grade),
        die_req: safeParseInt(editFormData.die_req, 0),
        rm_status: safeParseString(editFormData.rm_status),
        heat_code: safeParseString(editFormData.heat_code),
        remark: safeParseString(editFormData.remark),
      };

      console.log("Sending data to API:", updateData);

      // Make API call to update
      const response = await fetch(
        "http://localhost:8080/internal/weekly_plan",
        {
          method: "PATCH",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(updateData),
        }
      );

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`Failed to update: ${response.status} - ${errorText}`);
      }

      // Success - refresh data
      await fetchWeeklyPlan();
      setEditingRow(null);
      setEditFormData({});
      setMessage({
        type: "success",
        text: "Plan updated successfully!",
      });

      // Clear message after 3 seconds
      setTimeout(() => {
        setMessage({ type: "", text: "" });
      }, 3000);
    } catch (err) {
      console.error("Error updating plan:", err);
      setMessage({
        type: "error",
        text: `Failed to update plan: ${err.message}`,
      });

      // Clear error message after 5 seconds
      setTimeout(() => {
        setMessage({ type: "", text: "" });
      }, 5000);
    } finally {
      setSaving(false);
    }
  };

  // Handle cancel
  const handleCancel = () => {
    setEditingRow(null);
    setEditFormData({});
    setValidationErrors([]);
  };

  const editableFields = [
    "die_no",
    "plant_code",
    "forge_press",
    "customer",
    "prod_tonn",
    "section",
    "rm_grade",
    "die_req",
    "rm_status",
    "heat_code",
    "remark",
    "shift1_qty",
    "shift2_qty",
    "shift3_qty",
  ];

  // General field renderer for editable and static view
  const renderValue = (value, field, isEditing, rowKey) => {
    const isEditableField = editableFields.includes(field);

    if (isEditing && editingRow === rowKey && isEditableField) {
      const inputType = ["shift1_qty", "shift2_qty", "shift3_qty"].includes(
        field
      )
        ? "number"
        : "text";

      return (
        <input
          type={inputType}
          step="1"
          min="0"
          className="edit-input"
          value={editFormData[field] || ""}
          onChange={(e) => handleFieldChange(field, e.target.value)}
          placeholder="Enter value"
        />
      );
    }

    if (
      value === null ||
      value === undefined ||
      value === "" ||
      value === "N/A"
    ) {
      return <span className="na-value">N/A</span>;
    }

    if (["netWt", "prodTonn"].includes(field)) {
      const rounded = Math.round(Number(value));
      return isNaN(rounded) ? (
        <span className="na-value">N/A</span>
      ) : (
        rounded.toString()
      );
    }

    return value.toString();
  };

  // Quantity field-specific rendering (like customer dropdown)
  const renderQuantityValue = (plan, isEditing, rowKey, field) => {
    if (
      isEditing &&
      editingRow === rowKey &&
      matchingDieOptions.length > 1 &&
      ["customer", "forge_press", "plant_code", "prod_order"].includes(field)
    ) {
      const labelMap = {
        customer: "Customer",
        forge_press: "Press",
        plant_code: "Plant Code",
        prod_order: "Production Order",
      };

      const uniqueOptions = [
        ...new Set(
          matchingDieOptions.map((item) => {
            if (field === "prod_order") return item.prod_order;
            return item[field];
          })
        ),
      ].filter(Boolean);

      return (
        <select
          className="edit-input minwide-select"
          onChange={(e) => {
            const selectedValue = e.target.value;
            const matched = matchingDieOptions.find((item) => {
              if (field === "prod_order")
                return item.prod_order === selectedValue;
              return item[field] === selectedValue;
            });

            if (matched) {
              setEditFormData((prev) => ({
                ...prev,
                die_no: matched.die_no || "",
                plant_code: matched.plant_code || "",
                forge_press: matched.forge_press || "",
                customer: matched.customer || "",
                prod_order: matched.prod_order || "",
                prod_tonn: matched.net_wt || "",
                section: matched.section || "",
                rm_grade: matched.rm_grade || "",
                die_req: matched.die_req === "Yes" ? 1 : 0,
                rm_status: matched.rm_status || "",
                heat_code: matched.heat_code || "",
                remark: "",
              }));

              setMatchingDieOptions([]);
            }
          }}
          value={editFormData[field] || ""}
        >
          <option value="">Select {labelMap[field]}</option>
          {uniqueOptions.map((val, idx) => (
            <option key={idx} value={val}>
              {val}
            </option>
          ))}
        </select>
      );
    }

    // 👉 Non-edit display mode
    return plan[field] !== undefined &&
      plan[field] !== null &&
      plan[field] !== ""
      ? plan[field].toString()
      : "N/A";
  };

  // ✅ Correct – wrap in function
  const renderQtyCell = (plan, isEditing, rowKey) => {
    return renderValue(plan.qty, "qty", isEditing, rowKey);
  };

  const renderTableRows = () => {
    const workingDays = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
    const rows = [];

    workingDays.forEach((dayName, dayIndex) => {
      const dayPlans = groupedPlans[dayName] || [];

      if (dayPlans.length === 0) {
        // Empty day row
        rows.push(
          <tr key={`${dayName}-empty`}>
            <td className="weekly-plan-td weekly-day-cell">
              {dayPlans[0]?.dayLabel || dayName}
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td customer-cell">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td">
              <span className="na-value">N/A</span>
            </td>
            <td className="weekly-plan-td action-cell">
              <span className="na-value">-</span>
            </td>
          </tr>
        );
      } else {
        dayPlans.forEach((plan, planIndex) => {
          const rowKey = `${dayName}-${planIndex}`;
          const isEditing = editingRow === rowKey;

          rows.push(
            <tr key={rowKey} className={isEditing ? "editing-row" : ""}>
              {planIndex === 0 && (
                <td
                  className="weekly-plan-td weekly-day-cell"
                  rowSpan={dayPlans.length}
                >
                  {dayName}
                </td>
              )}
              <td className="weekly-plan-td">
                {renderQuantityValue(plan, isEditing, rowKey, "plant_code")}
              </td>

              <td className="weekly-plan-td max-ellipsis-90">
                {renderQuantityValue(plan, isEditing, rowKey, "prod_order")}
              </td>

              <td className="weekly-plan-td">
                {renderQuantityValue(plan, isEditing, rowKey, "forge_press")}
              </td>
              <td className="weekly-plan-td customer-cell">
                {renderQuantityValue(plan, isEditing, rowKey, "customer")}
              </td>

              <td className="weekly-plan-td">
                {renderValue(plan.netWt, "prod_tonn", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td">
                {renderValue(plan.dieNo, "die_no", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td pre-line">
                {["shift1_qty", "shift2_qty", "shift3_qty"].map(
                  (field, idx) => (
                    <div key={field} className="mb-2">
                      {renderQuantityValue(plan, isEditing, rowKey, field)}
                    </div>
                  )
                )}
              </td>

              <td className="weekly-plan-td">
                {renderValue(plan.prodTonn, "prod_tonn", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td">
                {renderValue(plan.section, "section", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td">
                {renderValue(plan.grade, "rm_grade", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td">
                {renderValue(plan.dieRequired, "die_req", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td">
                {renderValue(plan.rmStatus, "rm_status", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td">
                {renderValue(plan.heatCode, "heat_code", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td">
                {renderValue(plan.remark, "remark", isEditing, rowKey)}
              </td>
              <td className="weekly-plan-td action-cell">
                {isEditing ? (
                  <div className="flex-gap-2">
                    <button
                      className="action-btn save-btn"
                      onClick={() => handleSave(dayName, planIndex, plan)}
                      disabled={saving}
                      onMouseEnter={(e) => {
                        if (!saving) {
                          e.currentTarget.style.backgroundColor = "#007bff";
                          e.currentTarget.style.color = "white";
                        }
                      }}
                      onMouseLeave={(e) => {
                        if (!saving) {
                          e.currentTarget.style.backgroundColor = "white";
                          e.currentTarget.style.color = "#007bff";
                        }
                      }}
                    >
                      {saving ? "..." : <Save size={10} />}
                    </button>
                    <button
                      className="action-btn cancel-btn"
                      onClick={handleCancel}
                      disabled={saving}
                      onMouseEnter={(e) => {
                        if (!saving) {
                          e.currentTarget.style.backgroundColor = "#dc3545";
                          e.currentTarget.style.color = "white";
                        }
                      }}
                      onMouseLeave={(e) => {
                        if (!saving) {
                          e.currentTarget.style.backgroundColor = "white";
                          e.currentTarget.style.color = "#dc3545";
                        }
                      }}
                    >
                      <X size={10} />
                    </button>
                  </div>
                ) : (
                  <button
                    className="action-btn"
                    onClick={() => handleEditClick(dayName, planIndex, plan)}
                    onMouseEnter={(e) => {
                      e.currentTarget.style.backgroundColor = "#28a745";
                      e.currentTarget.style.color = "white";
                    }}
                    onMouseLeave={(e) => {
                      e.currentTarget.style.backgroundColor = "white";
                      e.currentTarget.style.color = "#28a745";
                    }}
                  >
                    <Edit size={12} />
                  </button>
                )}
              </td>
            </tr>
          );
        });
      }
    });

    return rows;
  };

  // Loading state
  if (loading) {
    return (
      <div className="weekly-plan-container">
        <div className="loading-state">
          <div className="large-icon mb-10">⏳</div>
          <h4>Loading Weekly Plan...</h4>
          <p>Fetching data from server</p>
        </div>
      </div>
    );
  }

  // Error state
  if (error) {
    return (
      <div className="weekly-plan-container">
        <div className="error-state">
          <h4>Error Loading Data</h4>
          <p>{error}</p>
          <button onClick={fetchWeeklyPlan} className="retry-btn">
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="weekly-plan-container">
      <div className="weekly-plan-header">
        <h3 className="weekly-plan-title">{weekTitle}</h3>
      </div>

      {validationErrors.length > 0 && (
        <div className="validation-error">
          <strong>Validation Errors:</strong>
          <ul className="validation-list">
            {validationErrors.map((error, index) => (
              <li key={index}>{error}</li>
            ))}
          </ul>
        </div>
      )}

      {message.text && (
        <div
          className={
            message.type === "success" ? "success-message" : "error-message"
          }
        >
          {message.text}
        </div>
      )}

      <div className="table-container">
        <table className="weekly-plan-table">
          <thead>
            <tr>
              <th className="weekly-plan-th">Day</th>
              <th className="weekly-plan-th">Plant Code</th>
              <th className="weekly-plan-th max-90">Production Order No</th>
              <th className="weekly-plan-th">Press</th>
              <th className="weekly-plan-th">Customer</th>
              <th className="weekly-plan-th">Net Wt</th>
              <th className="weekly-plan-th">Die No</th>
              <th className="weekly-plan-th">Qty (S1, S2, S3)</th>
              <th className="weekly-plan-th">Prod Tonn</th>
              <th className="weekly-plan-th">Section</th>
              <th className="weekly-plan-th">Grade</th>
              <th className="weekly-plan-th">Die Required</th>
              <th className="weekly-plan-th">RM Status</th>
              <th className="weekly-plan-th">Heat Code</th>
              <th className="weekly-plan-th">Remark</th>
              <th className="weekly-plan-th">Action</th>
            </tr>
          </thead>
          <tbody>{renderTableRows()}</tbody>
        </table>
      </div>
    </div>
  );
};

export default WeeklyPlanDisplay;
